@font-face {
  font-family: 'Munro';
  src: url('./fonts/munro/munro.ttf');
}

* {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Munro';
  font-size: 1.1rem;
}

body {
  background-color: #000;
  color: white;
}

#wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

#container {
  min-height: 30%;
}

#palette-container {
  display: flex;
  position: relative;
}

.square-container {
  width: 4vw;
  min-width: 3rem;
}

.square-container > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.square {
  height: 4vw;
  min-width: 4vw;
  min-height: 3rem;
}

.square-container input {
  font-size: 1.2rem;
  width: 100%;
  min-width: 30px;
  border: none;
  color: hsl(0, 0%, 40%);
  text-align: center;
}

.square-container input:focus {
  background-color: hsl(0, 0%, 10%);
  outline: none;
  color: white;
}

input {
  background-color: #000;
  color: white;
  border: none;
  text-align: center;
}

input:focus {
  background-color: hsl(0, 0%, 10%);
  outline: none;
  color: white;
}

.input-container input {
  width: 100%;
}

.bright-dark {
  display: flex;
  justify-content: space-between;
}

.bright-dark span:nth-child(2) {
  color: hsl(0, 0%, 50%)
}

.button-group > * {
  width: 100%;
  margin: 0.5rem 0;
}

.label-input {
  text-align: center;
}

.label-input > * {
  margin: 0.5rem 0.5rem;
  display: inline-block;
}

.label-input > input {
  width: 20%;
}

.hsb {
  position: absolute;
  left: -1em;
  bottom: 1rem;
  line-height: 1.45rem;
}

.hsb span {
  display: block;
}

.input-container {
  display: flex;
  justify-content: space-between;
  min-width: 300px;
  width: 30%;
}

.input-group { 
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-group input {
  display: block;
  width: 20%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}